/* Fonts */

/** 
* Default styles
*
*/

html,
body {
  font-size: 1.06rem;
  line-height: 1.7rem;
  letter-spacing: 0.03rem;
}

body {
  font-family: "Roboto";
  font-weight: 400;
}

span,
.copyright p.heading,
.contact p.heading {
  font-weight: 900;
}

p {
  margin-bottom: 1.6rem;
}

/* Main styles */

.showcase {
  display: flex;
  flex-flow: row wrap;
}

.showcase .columnLeft {
  flex: 10 1 auto;
  background: #666 url("../img/img_showcase01.jpg") no-repeat;
  background-size: cover;
}

.showcase .columnRight {
  flex: 1 1 300px;
  padding: 50px;
}

.showcase .columnRight .fa,
.showcase .columnRight .contact .social-list li:before,
.contact .social-list .showcase .columnRight li:before {
  margin-bottom: 3.3rem;
  font-size: 2.5rem;
  cursor: pointer;
}

.showcase .columnRight h1 {
  margin-bottom: 2.8rem;
  font-size: 2.4rem;
  line-height: 3.45rem;
}

.showcase .columnRight p {
  padding-right: 10px;
}

.services {
  display: flex;
  flex-flow: row wrap;
}

.services .serviceItem {
  flex: 1 1 32%;
  padding-top: 3.3rem;
  padding-left: 1.5rem;
  padding-right: 3rem;
  color: #fff;
}

.services .serviceItem .text {
  margin-bottom: 4rem;
}

.services .serviceItem h2 {
  margin-bottom: 1.4rem;
  font-size: 2.65rem;
  line-height: 3.9rem;
  font-weight: 300;
  letter-spacing: 0.08rem;
}

.services .serviceItem p {
  font-size: .95rem;
}

.services .serviceItem:nth-child(1) {
  background: #00b3ee url("../img/img_main01.jpg") no-repeat;
  background-size: cover;
}

.services .columnRight {
  display: flex;
  flex-flow: column wrap;
  flex: 1 1 40%;
}

.services .columnRight .serviceItem {
  padding-left: 3rem;
}

.services .columnRight .serviceItem:nth-child(1) {
  color: #000;
  background: #f2e5c5 url("../img/img_main02.jpg") no-repeat;
  background-size: cover;
}

.services .columnRight .serviceItem:nth-child(2) {
  background: #384146 url("../img/img_main03.jpg") no-repeat;
  background-size: cover;
}

.services .columnRight .serviceItem:nth-child(2) .btn {
  color: #fff;
  border-color: #fff;
}

.services .columnRight .serviceItem:nth-child(2) .btn:hover {
  color: #333;
  border-color: #333;
  background-color: #fff;
}

.services .columnRight .serviceItem p:last-of-type {
  margin-bottom: 3rem;
}

.services .columnRight .serviceItem .btn {
  padding: 0.7rem 2.3rem;
  font-size: .95rem;
  color: #333;
  border: 1px solid #333;
  border-radius: 10px;
}

.services .columnRight .serviceItem .btn:hover {
  color: #fff;
  background-color: #444;
  border-color: #fff;
}

/* Footer */

footer {
  display: flex;
  flex-flow: row wrap;
}

.copyright,
.contact {
  display: flex;
  flex-flow: column wrap;
  flex: 1 1 33.5%;
  padding-top: 3.5rem;
  padding-left: 1.5rem;
  color: #fff;
  background: #333 url("../img/img_footer01.jpg");
  background-size: cover;
}

.copyright h2,
.contact h2 {
  margin-bottom: 1.4rem;
  font-size: 2.65rem;
  line-height: 3.9rem;
  font-weight: 300;
  letter-spacing: 0.08rem;
}

.copyright p.heading,
.contact p.heading {
  margin-top: auto;
  margin-bottom: 7.3rem;
  font-size: 2.65rem;
  line-height: 3.9rem;
  letter-spacing: 0.08rem;
}

.contact {
  display: flex;
  flex-flow: column wrap;
  flex: 1 1 66%;
  padding-left: 3rem;
  background: #333 url("../img/img_footer02.jpg");
}

.contact h2 {
  margin-bottom: 2.4rem;
}

.contact form {
  flex: 1 1 auto;
  display: flex;
  flex-flow: row wrap;
  max-width: 620px;
  margin-bottom: 1.1rem;
}

.contact form .form-group:nth-child(1) {
  flex: 1 1 40%;
  margin-right: .5rem;
}

.contact form .form-group:nth-child(2) {
  flex: 1 1 40%;
}

.contact form .form-group:nth-child(3) {
  flex: 1 1 100%;
}

.contact form input,
.contact form textarea {
  padding: .9rem 1.6rem;
  color: #fff;
  border: 1px solid #FFF;
  background-color: transparent;
  border-radius: 15px;
}

.contact form input::placeholder,
.contact form textarea::placeholder {
  color: #fff;
}

.contact form textarea {
  height: 160px;
}

.contact form button {
  width: 250px;
  padding: 0.7rem 2.3rem;
  font-size: .95rem;
  color: #fff;
  background-color: transparent;
  border-color: #fff;
}

.contact form button:hover {
  color: #333;
  border-color: #333;
  background-color: #fff;
}

.contact .socialSlogan {
  margin-bottom: 1rem;
}

.contact .social-list {
  display: flex;
  flex-flow: row wrap;
  margin-bottom: 8rem;
}

.contact .social-list li {
  /* fa('.fa-shopping-bag'); */
  margin-right: 3px;
  font-size: 3rem;
  color: #345bb4;
  cursor: pointer;
}

.contact .social-list li:before {
  margin-right: 5px;
}

.contact .social-list li:nth-child(2) {
  /* fa('.fa-shopping-bag'); */
  color: #e64895;
}

.contact .social-list li:nth-child(2):before {
  margin-right: 5px;
}

.contact .social-list li:nth-child(3) {
  /* fa('.fa-shopping-bag'); */
  color: #00b4ff;
}

.contact .social-list li:nth-child(3):before {
  margin-right: 5px;
}

